.wrap-view-messange {
    display: flex;
    flex-direction: column;
    flex-grow: 0;
    flex-shrink: 1;
    background-color: white;
    width: 379px;
    border-left: 1px solid rgba(var(--sk_foreground_low,29,28,29),.13);

    // nick name side bar 
    .sidebar-nick-name-view-mes {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        flex-grow:0;
        flex-shrink: 1;
        padding: 8px 16px;
        border-top: 1px solid rgba(var(--sk_foreground_low,29,28,29),.13);
        border-bottom: 1px solid rgba(var(--sk_foreground_low,29,28,29),.13);
        margin-bottom: 10px;
    

        .sidebar-button-close-mes {
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 4px;
            height: 36px;
            width: 36px;
            opacity: 0.5;
            background-color: white;
            &:hover {
                background-color:  rgb(237, 237, 237);
                opacity: 1;
                cursor: pointer;
            }

            img {
                width: 13px;
            }
        }

        .thread-view{
            display: flex;
            flex-direction: column;
            width: 70%;

            button {
                display: flex;



                span {
                    display: flex;
                    flex-direction: row;
                    justify-content: flex-start;
                    align-items: center;
                    font-weight: 700;
                    font-size: 15px;
                    background-color: white;
                    font-family: 'Lato', sans-serif;
                    flex-grow:1;
                    line-height: 1.46668;
                    color: black;
                }
            }

            div {
                display: flex;

                span {
                    font-weight: 400;
                    font-size: 13px;
                    background-color: white;
                    font-family: 'Lato', sans-serif;
                    flex-grow:1;
                    color: black;
                    opacity: 0.7;

                    img{
                        width: 10px;
                    }
                }
            }
        }
    }

    .view-this-message {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: flex-start;
        padding:8px 0px 8px 16px;

        .user-this-message {
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            align-items: center;
            flex-grow: 0;
            margin-right: 8px;

            button {
                display: flex;
                justify-content: center;
                align-items: flex-end;
                line-height: 36px;
                border-radius: 4px;
                background-color: rgb(49, 49, 163);
                height: 36px;
                width: 36px;
                &:hover {
                    cursor: pointer;
                }

                img {
                    width: 32px;
                }
            }
        }

        .thisuser-message{
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            align-items: flex-start;
            flex-grow: 1;
            flex-shrink: 0;
            flex-wrap: wrap;
            max-width: 300px;

            .nick-name-user-date {
                display: flex;
                flex-direction: row;
                justify-content: flex-start;
                align-items: flex-end;
                flex-grow: 0;

                span {
                    margin-right: 5px;
                    font-weight: 700;
                    font-size: 15px;
                    font-family: 'Lato', sans-serif;
                }

                span:nth-child(2){
                    color: #616061;
                    font-weight: 400;
                    font-size: 12px;
                    
                }
            }

            .text-message{
                display: flex;
                flex-wrap: wrap;
                flex-direction: row;
                justify-content: flex-start;
                flex-grow: 0;
                flex-wrap: wrap;

                span {
                    color: #1d1c1de3;
                    line-height: 1.46668;
                    font-weight: 400;
                    font-size: 15px;
                    font-family: 'Lato', sans-serif;
                }
            }

        }
    }

    .message-line-replies{
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        max-width: 320px;
        margin:4px 0px 4px 16px;

        span{
            margin-right: 12px;
            color: rgba(var(--sk_foreground_max,29,28,29),.7);
            font-size: 13px;
            line-height: 1.38463;
            font-weight: 400;
            font-family: 'Lato', sans-serif;
        }

        hr {
            flex: 1;
            margin: 1px 0 0 0;
            border-top-color: rgba(var(--sk_foreground_low,29,28,29),.13);
        }
    }
}