.wrap-main-messange {
    display: flex;
    flex-direction: column;
    background-color: white;
    flex-grow:2;
    flex-shrink:0;
    width: 779px;

    .wrap-for-scroll-message{
        display: flex;
        flex-direction: column;
        background-color: white;
        flex-grow: 2;
        max-height: calc(100% - 172px);
        overflow-y: scroll;
        overflow-x: hidden;
        width: 100%;
        &::-webkit-scrollbar { width: 7px; height: 3px;}
        &::-webkit-scrollbar-track-piece { background-color: white;}
        &::-webkit-scrollbar-thumb { height: 50px; background-color: rgba(var(--sk_foreground_max, 29, 28, 29), 0.5); border-radius: 4px;}
    }

    .sidebar-mainmessage {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        padding: 7px 16px;
        flex-shrink: 0;
        border-top: 1px solid rgba(var(--sk_foreground_low, 29, 28, 29), 0.13);
        border-bottom: 1px solid rgba(var(--sk_foreground_low, 29, 28, 29), 0.13);
        margin-bottom: 10px;

        .wrap-sidebar-main-view-task{
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            align-items: flex-start;

            .sidebar-main-view-task{
                display: flex;
                flex-direction: row;
                justify-content: flex-start;
                align-items: center;

                .button-name-task{
                    display: flex;
                    justify-content: flex-start;
                    align-items: center;
                    line-height: 1.46668;
                    font-weight: 700;
                    font-size: 15px;
                    background-color: white;
                    font-family: 'Lato', sans-serif;
                    &:hover {
                        cursor: pointer;
                    }

                    img {
                        margin-right: 2px;
                        width: 13px;
                    }
                }

                .button-icon-sidebar-main{
                    display: flex;
                    flex-direction: row;
                    justify-content: center;
                    align-items: center;
                    flex-grow: 0;
                    flex-shrink: 0;
                    background-color: white;
                    width: 20px; 
                    height: 20px;
                    margin-left: 2px;
                    border-radius: 4px;
                    opacity: 0.6;
                    &:hover {
                        background-color:  rgb(237, 237, 237);
                        cursor: pointer;
                    }

                    img{
                        width: 13px;
                    }
                }
            }

            .button-much-user-mes{
                display: flex;
                flex-direction: row;
                justify-content: flex-start;
                align-items: center;

                button {
                    font-size: 13px;
                    line-height: 1.38463;
                    font-weight: 400;
                    font-family: 'Lato', sans-serif;
                    background-color: white;
                    color: rgba(var(--sk_foreground_max,29,28,29),.7);
                    &:hover {
                        color: blue;
                        cursor: pointer;
                    }

                    img {
                        margin-top: 2px;
                        margin-right: 2px;
                        width: 12px;
                    }
                }
                span {
                    font-size: 13px;
                    opacity: 0.8;
                    margin: 0 6px;
                }
            }
        }

        .wrap-sidebar-view-info-main{
            display: flex;
            flex-direction: row;
            justify-content: flex-end;
            align-items: center;

            button {
                display: flex;
                justify-content: center;
                align-items: center;
                border-radius: 4px;
                height: 36px;
                width: 36px;
                opacity: 0.5;
                background-color: white;
                &:hover {
                    background-color:  rgb(237, 237, 237);
                    opacity: 1;
                    cursor: pointer;
                }

                img {
                    width: 19px;
                }
            }
        }
    }

    .date-message{
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        flex-grow: 0;
        flex-shrink: 0;
        margin-left: 41%;
        margin-top: 4px;
        max-width: 170px;
        height: 28px;
        line-height: 27px;
        padding: 0 16px;
        border-radius: 24px;
        --saf-0: rgba(var(--sk_foreground_low,29,28,29),0.13);
        background: rgba(var(--sk_primary_background,255,255,255),1);
        box-shadow: 0 0 0 1px var(--saf-0),0 1px 3px 0 rgba(0,0,0,.08);
        opacity: 0.8;

        span {
            font-size: 13px;
            font-weight: 700;
            font-family: 'Lato', sans-serif;
        }

        img{
            margin-left: 4px;
            width: 8px;
        }
    }

    .view-this-message {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: flex-start;
        flex-wrap: wrap;
        padding:8px 0px 8px 16px;

        .main-message-files{
            display: flex;
            flex-direction: row;
            justify-content: flex-start;
            align-items: center;
            opacity: 0.5;
            margin-top: 2px;
            margin-left: 44px;

            span{
                font-weight: 700;
                font-size: 13px;
                background-color: white;
                font-family: 'Lato', sans-serif;
                line-height: 1.46668;
                color: black;
            }

            img{
                margin-left: 6px;
                width: 8px;
            }
        }

        .message-image {
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: flex-start;
            align-items: center;
            margin-top: 2px;
            margin-left: 44px;

            img{
                width: 40%;
                margin: 10px 0px 10px;
            }

            video{
                width: 40%;
                margin: 10px 0px 10px;
            }
        }

        .viewuser-message-comments{
            display: flex;
            flex-direction: row;
            justify-content: flex-start;
            align-items: center;
            margin-top:2px;
            margin-left: 44px;

            button {
                display: flex;
                justify-content: center;
                align-items: flex-end;
                line-height: 36px;
                margin-right:3px;
                border-radius: 4px;
                background-color: rgb(45, 143, 105);
                height: 24px;
                width: 24px;
                &:hover {
                    cursor: pointer;
                }

                img {
                    width: 20px;
                }
            }

            span {
                justify-content: flex-start;
                align-items: center;
                flex-grow: 0;
                flex-shrink: 0;
                margin-right: 3px;
                font-weight: 700;
                font-size: 13px;
                background-color: white;
                font-family: 'Lato', sans-serif;
                line-height: 1.46668;
                color: rgba(var(--sk_highlight,18,100,163),1);
                &:hover {
                    text-decoration: underline;
                    cursor: pointer;
                }
            }
            span:nth-child(3) {
                opacity: 0.7;
                font-weight: 400;
                color:black;
            }
        }

        .user-this-message {
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            align-items: center;
            flex-grow: 0;
            max-width: 37px;
            margin-right: 8px;

            button {
                display: flex;
                justify-content: center;
                align-items: flex-end;
                line-height: 36px;
                border-radius: 4px;
                background-color: rgb(49, 49, 163);
                height: 36px;
                width: 36px;
                &:hover {
                    cursor: pointer;
                }

                img {
                    width: 32px;
                }
            }
        }

        .thisuser-message{
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            align-items: flex-start;
            flex-grow: 1;
            flex-shrink: 0;
            flex-wrap: wrap;
            width: calc(100% - 67px);

            .nick-name-user-date {
                display: flex;
                flex-direction: row;
                justify-content: flex-start;
                align-items: flex-end;
                flex-grow: 0;

                span {
                    margin-right: 5px;
                    font-weight: 700;
                    font-size: 15px;
                    font-family: 'Lato', sans-serif;
                }

                span:nth-child(2){
                    color: #616061;
                    font-weight: 400;
                    font-size: 12px;
                    
                }
            }

            .text-message{
                display: flex;
                flex-wrap: wrap;
                flex-direction: row;
                justify-content: flex-start;
                flex-grow: 0;
                flex-wrap: wrap;

                span {
                    color: #1d1c1de3;
                    line-height: 1.46668;
                    font-weight: 400;
                    font-size: 15px;
                    font-family: 'Lato', sans-serif;
                }
            }

        }
    }
}