$padding: 0 16px;
$background-color-hover: #350d36;
$background-color-button: rgb(82,38,83);
$background: #3F0E40;

.wrap-channel-sidebar {
    display: flex;
    flex-direction: column;
    flex-grow: 0;
    flex-shrink: 0;
    width: 207px;
    background-color: $background;


    // nick name side bar 
    .sidebar-nick-name {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        flex-grow:0;
        flex-shrink: 1;
        padding: 8px 16px;
        border-top:  1px solid rgba(117, 67, 117, 0.561);
        border-bottom:  1px solid rgba(117, 67, 117, 0.561);
        margin-bottom: 10px;
        &:hover {
            background-color: $background-color-hover;
            cursor: pointer;

            .nick-name {
                span {
                background-color: $background-color-hover;
                opacity: 1;
                }
            }
        }

        .sidebar-button-edit-nick-name {
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 18px;
            height: 36px;
            width: 36px;
            &:hover {
                cursor: pointer;
            }

            img {
                width: 20px;
            }
        }

        .nick-name{
            display: flex;
            flex-direction: column;
            width: 70%;

            button {
                display: flex;



                span {
                    display: flex;
                    flex-direction: row;
                    justify-content: flex-start;
                    align-items: center;
                    font-weight: 700;
                    font-size: 15px;
                    background-color: $background;
                    font-family: 'Lato', sans-serif;
                    flex-grow:1;
                    line-height: 1.46668;
                    color: white;

                    img {
                        width: 8px;
                    }
                }
            }

            div {
                display: flex;

                span {
                    font-weight: 400;
                    font-size: 13px;
                    background-color: $background;
                    font-family: 'Lato', sans-serif;
                    flex-grow:1;
                    color: white;
                    opacity: 0.8;

                    img{
                        width: 15px;
                    }
                    img:nth-child(1) {
                        width: 10px;
                    }
                }
            }
        }
    }

    //sidebar with channels, thread, task, question and etc...
    .button-sidebar-check-task{
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        flex-grow: 0;
        flex-shrink: 0;
        padding: 0 16px;
        background-color: $background;
        &:hover {
            background-color: $background-color-hover;
            cursor: pointer;
            span{
                opacity: 1;
            }
        }

        span {
            display: flex;
            align-items: center;
            font-weight: 400;
            font-size: 15px;
            font-family: 'Lato', sans-serif;
            color: white;
            margin: 8px 0;
            opacity: 0.8;

            img {
                padding-top: 1px;
                width: 12px;
            }
        }

        .icon-chennel {
            img{
                width: 14px;
            }
        }
    }

    //view channels sidebar 

    .button-sidebar-check-task-channels{
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        flex-grow: 0;
        flex-shrink: 0;
        padding: 0 16px;
        background-color: $background;
        border-top:  1px solid rgba(117, 67, 117, 0.561);
        margin-top: 10px;
        &:hover {
            cursor: pointer;
        }

        span,div {
            display: flex;
            flex-direction: row;
            justify-content: center;
            align-items: center;
            font-weight: 400;
            font-size: 15px;
            font-family: 'Lato', sans-serif;
            color: white;
            margin: 8px 0;
            opacity: 0.8;

            img {
                padding-top: 1px;
                width: 8px;
            }

            button {
                display: flex;
                justify-content: center;
                align-items: center;
                background-color: $background;
                padding: 5px;
                border-radius: 4px;
                margin-left: 70px;
                &:hover {
                    cursor: pointer;
                    background-color: $background-color-button;
                }

                img{
                    width: 17px;
                }
            }

        }
    }
}